@import url(https://fonts.googleapis.com/css2?family=Chilanka&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F4FEFF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Chilanka', cursive;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 1000px) {
  * {
    font-size: 12px;
  }
}

@media (max-height: 700px) {
  * {
    font-size: 12px;
  }
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}
.internal-container {
  margin: auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  height: 60vh;
}
.logo {
  width: 200px;
  border-radius: 50px;
}
.login-button {
  width: 300px;
}
.infoContainer {
  background-color: #ffedd2;
  text-align: left;
  padding: 20px;
}
@media screen and (max-width: 600px) {
  .container {
    height: 100vh;
  }
  .internal-container {
    height: 100vh;
  }
}

.page {
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.homepage-content {
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 1200px;
  height: auto;
  margin:auto;
  padding: 30px 50px;
  border: 1px solid;
  border-radius: 10px;
}

.button-group {
  padding: 30px 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.info {
  padding-top: 10px;
  text-align: start;
}

.profile-picture {
  Width: 10%;
  margin: 20px auto;
}

.homepage-content .card-header.collapsed {
  background:#FFEDD2;
}

.homepage-content .card-header:not(.collapsed) {
  background:#FFEDD2;
}

.homepage-content .card-body {
  background:#FFEDD2;
}
.page {
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.content {
  text-align: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 1200px;
  height: 90%;
  max-height: 90%;
  margin: auto;
  padding: 30px 50px;
  border: 1px solid;
  border-radius: 10px;
}
.button-group {
  padding: 0 0 30px 0;
}
.profilecontainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}
.profile {
  height: 70%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.name {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-basis: 0;
}
.picture {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.nameTitle {
  width: 30%;
  margin: 0;
}
.nameInput {
  width: 70%;
}
.profile-picture {
  width: 150px;
}

.fileinput {
  background-color: #008cba;
  border: 2px solid #008cba;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 12px;
  transition-duration: 0.4s;
  cursor: pointer;
}
.fileinput:hover {
  background-color: white;
  color: black;
}

.pickIcon {
  width: 100%;
}
.savebutton {
  width: 80%;
}
.pokemonImg {
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.game-lobby-page {
  height: 95vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.game-lobby-content {
  text-align: start;
  background-color: white;
  display: flex;
  flex-direction: row;
  width: 90%;
  max-width: 1200px;
  height: 90%;
  max-height: 90%;
  margin: auto;
  padding: 30px 50px;
  border: 1px solid;
  border-radius: 10px;
}

.settings {
  width: 50%;
  padding-right: 30px;
  border-right: 1px solid;
  display: flex;
  flex-direction: column;
}

.players {
  width: 50%;
  padding: 40px;
}

.player-container {
  width: 90px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  overflow-wrap: break-word;
}

.player-pfp {
  width: 80px;
  margin: auto;
}

.settings-form {
  padding: 10px;
  margin-bottom: auto;
  min-height: 70%;
  overflow-y: auto;
  overflow-x: hidden;
}

.start-game-btn {
  margin-top: 10px;
  margin-left: 10px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.leave-lobby-btn {
  margin-top: 10px;
  margin-left: auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.player-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
}
.kickButton {
  width: 20px;
  height: 20px;
  text-align: center;
  padding: 0 !important;
  border-radius: 50% !important;
}

.words-modal {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin: auto;
}

.words-modal .card-header {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background:#FFEDD2;
}

.words-modal .card-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  background:#FFEDD2;
}

.word-btn {
  margin: 0px 5px;
  background-color: white !important;
}

.word-btn:hover {
  margin: 0px 5px;
  color: black !important;
  background-color: lavenderblush !important;
}

.canvas-container {
  position: relative;
}

#canvas {
  height: 100%;
  width: 100%;
}

canvas {
  border: 1px solid;
  border-radius: 0.25rem;
}

.canvas-tool-container {
  height: 30%;
  margin-top: 10px;
  display: flex;
  background-color: white;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid;
  border-radius: 0.25rem;
}

.color-options-container {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  width: 70%;
  max-width: 70%;
  padding: 2px;
}

.color-option {
  border-radius: 100%;
  max-width: 60px;
  width: 60%;
  position: relative;
  height: auto;
  cursor: pointer;
  margin: 2px;
  border: none;
}

.color-option:after {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.col-content {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.timer {
  width: 5%;
  height: auto;
}

.tools {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: space-evenly;
  align-items: center;
  grid-gap: 10%;
  gap: 10%;
  width: 20%;
  max-width: 20%;
  height: auto;
}

.tools Button {
  padding: inherit;
  height: auto;
}

.custom-modal {
  width: 100%;
  position: absolute;
  padding-top: 10%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 0.25rem;
}

.player-sidebar {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  /* width: 100%; */
}

.player-score-container {
  border: 1px solid;
  border-radius: 10px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  background-color: white;
  display: flex;
  flex-direction: row;
}

.player-sidebar .card-header {
  background-color: rgba(255, 255, 255, .8);
  padding: 4px;
}

.player-sidebar .card-body {
  background-color: rgba(255, 255, 255, .8);
  padding: 7px;
}

.arrow::after, .bs-popover-right .arrow::after {
  border-right-color: lavender !important;
}

.report-popover {
  background-color: lavender;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 10px;
}

.report-popover Button {
  background-color: white;
  margin-left: 5px;
}

.report-popover span {
  display: inline-flex;
  align-items: center;
}

.player {
  margin-bottom: 5px;
  width: 100%;
  max-width: 100%;
}
.game-page {
  display: flex;
  flex-direction: column;
}

.game-contents {
  height: 70vh;
